#trip_planner{
     label span {
          display: inline-flex;
          align-items: center;
          justify-content: center;
          width: 24px;
          height: 24px;
          color: var(--secondary);
          font-weight: 700;
          background-color: var(--white);
          padding: 5px;
          margin: 0 9px 0 0;
          border-radius: 50%;
     }

     .time_date{
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          gap: var(--spacereg);
     }
}